*{ 
    margin: 0;
    padding: 0;
}
body{
    background-color: rgb(190, 190, 190);
}
.hide{
    display: none;
}
.outer {
    background-color: white;
    font-weight: 600;
    margin:auto ;
    margin-top: 130px;
    margin-bottom: 100px;
    border: 2px solid rgb(49, 44, 215);
    border-radius: 16px;
    width: 500px;
    height: 450px;
}
.underline{
    width: 60px;
    height: 2px;
    background-color: rgb(17, 0, 255);
}
.head{
    color: blue;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top:40px ;
    text-align: center;
    font-size: 25px;
}
.inputs{   
 margin-top:30px ;
display: flex;
flex-direction: column;
justify-content:center ;
align-items: center;
gap: 30px;
}
.inputs input{
    color: black;
    /* background-color: gray; */
    font-size: 18px;
    width: 300px;
    height: 48px;
    padding: 20px;
}
.submit{
    margin-top:20px ;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 100px;
}
.forget{
    display: flex;
    justify-content: center;
    margin-top: 20px;
    gap: 10px;

}
button signup,login{
     width: 100px;
}